import React, { Component } from 'react';

// -- json data
import CalloutImageJson from '../../static/json/map_callouts.json';

class callout_image_box extends Component {
	state = {
		timer: 0,
		current_index: 0,
		activeMap: ''
	};

	componentDidMount() {
		setInterval(() => {
			if (this.state.timer === 5) {
				this.setState({ timer: 0 });
				// -- show answer, set another interval?
				// -- then go to next image
				this.setState({ current_index: this.state.current_index + 1 });
			} else {
				this.setState({ timer: this.state.timer + 1 });
			}
		}, 1000);
		this.setState({ activeMap: this.props.activeMap });
	}

	render() {
		return (
			<div>
				<h2>test</h2>
				counter: {this.state.timer}
				<br />
				image:
				{CalloutImageJson.map(current => {
					if (current.map_name === this.props.activeMap) {
						return current.images[this.state.current_index].image_path;
					}
				})}
			</div>
		);
	}
}

export default callout_image_box;
